<template>
  <img alt="Vue logo" src="./assets/logo.png" />
  <br />
  <button v-on:click="showData">คลิกเพื่อดูข้อมูล</button>
  <button @click="showDatas">คลิกเพื่อดูข้อมูล</button>
  <br />
  <button @click="plus">เพิ่มอายุ</button>

  <button @click="pluss(5)">เพิ่มอายุทีละ5</button>

  <button @click="mi">ลดอายุ</button>
  <button @click="mis(10)">ลดอายุทีละ5</button>

  <form>
    <label for="">ป้อนชื่อ : </label>
    <input type="text" v-on:input="setfirstname" />
    <br />
    <label for="">ป้อนนามสกุล : </label>
    <input type="text" v-on:input="setlastname" />
    <br />
    <label for="">ป้อนชื่อเล่น : </label>
    <input type="text" v-on:input="setnickname" />
    <br />
    <label for="">ป้อนอายุ : </label>
    <input type="text" v-on:input="setage" />
    <br />
    <label for="">ป้อน วัน/เดือน/ปี เกิด : </label>
    <input type="text" v-on:input="setdate" />
    <br />
    <label for="">ป้อนสาขา : </label>
    <input type="text" v-on:input="setsacar" />
    <br />
    <label for="">ป้อนห้อง : </label>
    <input type="text" v-on:input="setroom" />
    <br />
    <label for="">ป้อนคติประจำใจ : </label>
    <input type="text" v-on:input="setcardi" />
    <br />
    <label for="">ป้อนที่เรียนต่อ : </label>
    <input type="text" v-on:input="setstudycontinue" />
    <br />
    <label for="">ป้อนวิชาที่ไม่ชอบ : </label>
    <input type="text" v-on:input="setsubject" />
    <br />
    <button type="submit">กดดิ์</button>
  </form>

  <h2>ชื่อจริง : {{ firstname }}</h2>
  <h2>นามสกุล : {{ lastname }}</h2>
  <h2>ชื่อเล่น : {{ nickname }}</h2>
  <h2>อายุ : {{ age }}</h2>
  <h2>วัน/เดือน/ปีเกิด : {{ date }}</h2>
  <h2>สาขา : {{ sacar }}</h2>
  <h2>ห้อง : {{ room }}</h2>
  <h2>คติประจำใจ : {{ cardi }}</h2>
  <h2>เรียนต่อที่ : {{ studycontinue }}</h2>
  <h2>วิชาที่ไม่ชอบ : {{ subject }}</h2>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      firstnames: "วรศาสตร์",
      lastnames: "เตรียมวิศิษฎ์",
      firstname: "",
      lastname: "",
      nickname: "",
      age: "",
      date: "",
      sacar: "",
      room: "",
      cardi: "",
      studycontinue: "",
      subject: "",
    };
  },
  methods: {
    showData() {
      alert(this.firstnames);
    },
    showDatas() {
      alert(this.lastnames);
    },
    plus() {
      this.age++;
    },
    mi() {
      this.age--;
    },
    pluss(val) {
      this.age += val;
    },
    mis(val) {
      this.age -= val;
    },

    setfirstname() {
      this.firstname = event.target.value;
    },
    setlastname() {
      this.lastname = event.target.value;
    },
    setnickname() {
      this.nickname = event.target.value;
    },
    setage() {
      this.age = event.target.value;
    },
    setdate() {
      this.date = event.target.value;
    },
    setsacar() {
      this.sacar = event.target.value;
    },
    setroom() {
      this.room = event.target.value;
    },
    setcardi() {
      this.cardi = event.target.value;
    },
    setstudycontinue() {
      this.studycontinue = event.target.value;
    },
    setsubject() {
      this.subject = event.target.value;
    },
  },
};
</script>

